body {
  margin: 0;
  font-family: 'Arial', sans-serif;
  background-color: #0a0f24;
  color: #e0e0e0;
}

.app {
  text-align: center;
  padding: 20px;
}

.app-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #151a2e;
  padding: 20px;
  border-radius: 10px;
  color: #35c9e7;
}

.header-stats div {
  margin: 0 10px;
}

.price-info {
  margin: 20px 0;
}

.best-price h1 {
  font-size: 3em;
  margin: 0;
}

.ticker-table {
  background-color: #151a2e;
  padding: 20px;
  border-radius: 10px;
}

.table-header, .table-row {
  display: flex;
  justify-content: space-between;
  padding: 10px 0;
  border-bottom: 1px solid #2d2f43;
}

.table-header div, .table-row div {
  flex: 1;
}

.table-row {
  color: #fff;
}

.table-row:nth-child(odd) {
  background-color: #1b2133;
}

.table-row:nth-child(even) {
  background-color: #111527;
}

.table-row div {
  text-align: left;
}

.table-header div {
  font-weight: bold;
}

.table-row div:nth-child(1),
.table-header div:nth-child(1) {
  flex: 0.5;
  text-align: center;
}
